import axios from './api/axios';

const SearchService = {
  async searchJuvenileInCenters(params) {
    return axios.get(`/api/juvenile/juveniles/search_juvenile/`, {
      params,
    });
  },
  async searchInspectorsInCenters(params) {
    return await axios.get('api/info/get_prophylactic/', {
      params,
    });
  },
};
export default SearchService;
