String.prototype.isUpperCase = function () {
  if (this == this.toUpperCase()) {
    return true;
  } else return false;
};

export const useLatinToCyrilic = async (text = '') => {
  if (text.isUpperCase()) {
    Object.entries(translit).forEach(([key, value]) => {
      text = text.replace(new RegExp(key, 'g'), value);
    });
    return text.toUpperCase();
  } else {
    Object.entries(translit).forEach(([key, value]) => {
      text = text.replace(new RegExp(key, 'g'), value);
    });
    return text;
  }
};

(async () => {
  const res = await useLatinToCyrilic(`Sud-ekspertlik faoliyatini rivojlantirishni qo'llab-quvvatlash jamg'armasi faoliyatini boshqarish kengashining dastlabki yig'ilishi bo'lib o'tdi`);
  console.log(res);
})();
