import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useSchoolList() {
  const schoolList = ref([]);
  const fetchSchoolsByDistrictId = async (val) => {
    console.log(val);
    const {data} = await InfoService.fetchSchoolList(val);
    schoolList.value = data;
  };

  return {
    schoolList,
    fetchSchoolsByDistrictId,
  };
}
