<template>
  <form v-if="parentTypesList.length" @submit="onSubmit">
    <h3 class="my-4 text-2xl">Bolaning boquvchisi haqida ma'lumotlar</h3>
    <span class="error_message">{{ maritalStatusError }}</span>
    <div v-if="maritalStatus !== HAS_NOT_PARENTS" class="grid grid-cols-1 md:grid-cols-2 gap-4 2xl:w-[70%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="parent_type"> Boquvchining turi </label>
        <SelectBox name="parent_type" id="parent_type" v-model="parent_type" :options="filteredParentTypeList" show-by="text" track-by="id" placeholder="Boquvchining turini tanlang" />
        <span>{{ parent_typeError }}</span>
      </div>
      <div class="flex gap-4 my-3">
        <div class="form_wrapper">
          <label class="input_label" for="passport_seria"> Hujjat seriasi </label>
          <input v-maska data-maska="@@" id="passport_seria" v-model="passport_seria" placeholder="" class="base_input uppercase w-[150px]" name="passport_seria" type="text" />
        </div>
        <div class="form_wrapper w-full">
          <label class="input_label" for="passport_number">Hujjat raqami</label>
          <input id="passport_number" v-maska data-maska="#######" v-model="passport_number" placeholder="" class="base_input uppercase" name="passport_number" type="text" />
        </div>
      </div>
      <div class="flex items-center">
        <div class="form_wrapper my-3">
          <label class="input_label" for="parent_pinfl"> JSHSHIR </label>
          <div class="flex items-center gap-2">
            <input v-validate-cyrillic id="parent_pinfl" v-model="parent_pinfl" v-maska data-maska="##############" placeholder="JSHSHIR ni kiriting" class="base_input" name="parent_pinfl" />
            <button class="self-start w-[150px] px-[12px] py-[6px] bg-primary h-[45px] text-white hover:opacity-40 rounded" @click.prevent="getProfilacticInspectors">Qidirish</button>
          </div>
          <span class="error_message">{{ pinflError }}</span>
        </div>
      </div>
    </div>
    <div v-if="maritalStatus !== HAS_NOT_PARENTS" class="grid grid-cols-2 md:grid-cols-3 gap-4 2xl:w-[60%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="parent_last_name"> Familiya </label>
        <input
          v-validate-cyrillic
          :disabled="isIntegrationData"
          id="parent_last_name"
          v-model="parent_last_name"
          class="base_input"
          name="parent_last_name"
          type="text"
          placeholder="Familiyasini kiriting"
        />
        <span>{{ last_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="parent_first_name"> Ismi </label>
        <input
          v-validate-cyrillic
          :disabled="isIntegrationData"
          id="parent_first_name"
          v-model="parent_first_name"
          placeholder="Ismini kiriting"
          class="base_input"
          name="parent_first_name"
          type="text"
        />
        <span>{{ first_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="parent_father_name"> Sharifi </label>
        <input
          v-validate-cyrillic
          :disabled="isIntegrationData"
          id="parent_father_name"
          v-model="parent_father_name"
          class="base_input"
          name="parent_father_name"
          type="text"
          placeholder="Sharifini kiriting"
        />
        <span>{{ father_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Tug‘ilgan sanasi </label>
        <DateComponent
          v-model="parent_birth_date"
          placeholder="Sanani tanlang"
          :disabled="isIntegrationData"
          :options="{
            maxDate: dayjs().subtract(18, 'year').toDate(),
            startDate: dayjs().subtract(30, 'year').toDate(),
          }"
        />
        <span>{{ birth_dateError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Qidiruvdami </label>
        <ButtonGroup :key="is_wanted" id="is_wanted" v-model:selected-item="is_wanted" :titles="btnGroupYesNo" />
        <span class="error_message">{{ is_wantedError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Chet Eldami </label>
        <ButtonGroup :key="is_abroad" id="is_abroad" v-model:selected-item="is_abroad" :titles="btnGroupYesNo" />
        <span class="error_message">{{ is_abroadError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="employment"> Qisqacha ma`lumot </label>
        <textarea
          id="employment"
          v-model="employment"
          class="base_input h-24"
          name="employment"
          type="textarea"
          placeholder="Boquvchisi haqida qisqacha ma`lumot kiriting (yashash manzili,ish joyi, telefon raqami h.k)"
        />
        <span>{{ employmentError }}</span>
      </div>
    </div>
    <button class="base_input px-5 bg-primary text-white border-primary hover:opacity-50" type="submit">Boquvchini qo'shish</button>
    <h1 class="text-primary text-[1.2em] mt-[30px] mb-[12px]">Boquvchilar:</h1>
    <section v-if="initialParentsList.length || parentsList.length" class="col-span-2 md:col-span-3 grid grid-cols-2 md:grid-cols-3 gap-4 mb-[34px]">
      <ParentInfoCard v-for="p in normalizedParentsList" :key="p.pinfl" :parent-object="p" @delete-parent="deleteParent" />
    </section>
  </form>
</template>

<script setup>
import {vMaska} from 'maska';
import * as yup from 'yup';
import {useField, useForm} from 'vee-validate';
import SelectBox from './Common/SelectBox';
import {computed, defineEmits, defineProps, ref, toRefs, watch} from 'vue';
import ParentInfoCard from './ParentInfoCard';
import useParentTypes from '../composables/selectbox-list/useParentTypes';
import useScrollToError from '../composables/useScrollToError';
import dayjs from 'dayjs';
import {minValidatorMessage, requiredValidatorMessage} from '../utils/validatorMessages';
import SearchService from '../services/search.service';
import {btnGroupYesNo} from '../utils/buttonGroupTitles';
import ButtonGroup from './Common/ButtonGroup';
import DateComponent from './Common/DateComponent';
import useAlert from '../composables/useAlert';
import {GCP} from '../services/gcp.service';
import {useCyrillicToLatin} from '../composables/useCyrillicToLatin';

const isIntegrationData = ref(false);

const HAS_NOT_PARENTS = 4;

const props = defineProps({
  maritalStatus: {
    type: [Number, undefined],
  },
  parentsList: {
    type: Array,
    default: function () {
      return [];
    },
  },
  initialParentsList: {
    type: Array,
    default: function () {
      return [];
    },
  },
});
const emit = defineEmits(['update:parents-list', 'delete-parent']);

const {scrollToError} = useScrollToError();
const {parentTypesList} = useParentTypes();

const {handleSubmit, resetForm} = useForm();

const {alertIt} = useAlert();

const {maritalStatus, parentsList, initialParentsList} = toRefs(props);

const filteredParentTypeList = computed(() => {
  if (parentTypesList.value.length) {
    const usedParentTypes = [...parentsList.value.map((p) => p.parent_type), ...initialParentsList.value.map((p) => p.parent_type)];
    return parentTypesList.value.filter((type) => !usedParentTypes.includes(type.id));
  } else return [];
});

const allowedToSearchParent = computed(() => {
  return parent_pinfl.value.length === 14;
});

const normalizedParentsList = computed(() => {
  return [...parentsList.value, ...initialParentsList.value].map((parent) => {
    return {
      ...parent,
      parent_type_title: parentTypesList.value.filter((type) => type.id === +parent.parent_type)[0].text,
    };
  });
});

const maritalStatusError = ref('');

function deleteParent(parentObj) {
  emit('delete-parent', parentObj);
}

watch(
  () => maritalStatus.value,
  (newStatus, oldStatus) => {
    if (newStatus !== oldStatus) {
      maritalStatusError.value = '';
    }
    if (newStatus === HAS_NOT_PARENTS) {
      emit('update:parents-list', []);
    }
  }
);

const requestPassportData = async () => {
  try {
    const resp = await GCP.getPassportData({
      pinfl: parent_pinfl.value,
      passport_data: `${passport_seria.value.toUpperCase()}${passport_number.value}`,
    });
    if (resp.status === 200) {
      parent_first_name.value = await useCyrillicToLatin(resp.data?.first_name);
      parent_father_name.value = await useCyrillicToLatin(resp.data?.father_name);
      parent_last_name.value = await useCyrillicToLatin(resp.data?.last_name);
      parent_birth_date.value = new Date(resp.data?.birth_date);
      isIntegrationData.value = true;
    }
  } catch (error) {
    alertIt(error?.response?.data?.detail, 'error');
  }
};

async function getProfilacticInspectors() {
  const params = {
    pinfl: parent_pinfl.value,
  };
  try {
    const resp = await SearchService.searchInspectorsInCenters(params);
    if (!resp.results.length) {
      alertIt(`Kiritilgan JSHSHIR bo'yicha ota-ona topilmadi`, 'info');
    } else {
      alertIt(`Kiritilgan JSHSHIR bo'yicha ota-ona topilmadi`, 'info');
      emit('update:parents-list', [...parentsList.value, ...resp.results]);
      // existProfilacticInspectorsFromServer.value = resp.results
    }
  } catch (e) {
    try {
      await requestPassportData();
    } catch (error) {
      alertIt(error?.response?.data?.detail || "Ma'lumot kelishida hatolik mavjud", 'error');
    }
  }
}

function onInvalidSubmit({errors}) {
  scrollToError(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(({parent_birth_date, parent_first_name, parent_last_name, parent_father_name, parent_pinfl, ...values}) => {
  if (!maritalStatus.value) {
    maritalStatusError.value = 'Bolaning holati tanlanishi shart!';
    return;
  }
  const parentObj = {
    ...values,
    first_name: parent_first_name,
    last_name: parent_last_name,
    father_name: parent_father_name,
    pinfl: parent_pinfl,
    birth_date: dayjs(parent_birth_date).format('YYYY-MM-DD'),
  };
  emit('update:parents-list', [...parentsList.value, parentObj]);
  resetForm();
}, onInvalidSubmit);

const passport_seria = ref('');

const passport_number = ref('');

const {value: parent_type, errorMessage: parent_typeError} = useField('parent_type', yup.number().required(requiredValidatorMessage));

const {value: parent_first_name, errorMessage: first_nameError} = useField('parent_first_name', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: parent_last_name, errorMessage: last_nameError} = useField('parent_last_name', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: parent_father_name, errorMessage: father_nameError} = useField('parent_father_name', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: employment, errorMessage: employmentError} = useField('employment', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: parent_birth_date, errorMessage: birth_dateError} = useField('parent_birth_date', yup.string(), {
  initialValue: '',
});

const {value: parent_pinfl, errorMessage: pinflError} = useField('parent_pinfl', yup.string().min(14, minValidatorMessage).required(), {
  initialValue: '',
});

const {value: is_abroad, errorMessage: is_abroadError} = useField('is_abroad', yup.string().required(requiredValidatorMessage));

const {value: is_wanted, errorMessage: is_wantedError} = useField('is_wanted', yup.string().required(requiredValidatorMessage));
</script>

<style scoped>
.section_wrapper {
  @apply px-3 py-2  my-5;
}

span {
  @apply text-red-400 h-5;
}
</style>
