import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useTexnikumsList() {
  const texnikumsList = ref([]);
  const fetchTexnikumsListByRegionId = async (val) => {
    const {data} = await InfoService.fetchTexnikumsListByRegionId(val);
    texnikumsList.value = data;
  };

  return {
    texnikumsList,
    fetchTexnikumsListByRegionId,
  };
}
