<template>
  <h3 class="my-4 text-2xl">Bolaning holati</h3>
  <GuardianInfoCard v-if="selectedJuvenile.parent_info" :parent_info="selectedJuvenile.parent_info" />
  <div v-if="selectedJuvenile.parent_info && selectedJuvenile?.is_been_in_center" class="flex justify-end my-[24px]">
    <button v-if="!changeMode" class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(true)">O'zgartirish</button>
    <button v-else class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(false)">Bekor qilish</button>
  </div>
  <div v-if="changeMode || !selectedJuvenile.parent_info" class="p-[12px] shadow">
    <ChildsGuardianForm :clear-form-mode="clearForm" @submit="onSubmit" />
  </div>
</template>

<script setup>
import {defineEmits, defineProps, ref, toRefs} from 'vue';
import useClearForm from '../../../../composables/useClearForm';
import useAlert from '../../../../composables/useAlert';
import ParentsService from '../../../../services/parents.service';
import JuvenilesService from '../../../../services/juveniles.service';
import GuardianInfoCard from './GuardianInfoCard';
import ChildsGuardianForm from './ChildsGuardianForm';

const emit = defineEmits(['select-juvenile', 'save-juvenile-handler']);
const props = defineProps({
  selectedJuvenile: {
    type: Object,
    required: true,
  },
});
const {selectedJuvenile} = toRefs(props);
const {clearForm, setClearForm} = useClearForm();
const {alertIt} = useAlert();
const {createJuvenileParents, createJuvenileGuardianInfo} = ParentsService;
const {fetchJuvenileInfoDetailById} = JuvenilesService;
const onSubmit = async ({marital_status, parents}) => {
  try {
    const {
      data: {id: juvenileParentInfoId},
    } = await createJuvenileGuardianInfo({marital_status}, selectedJuvenile.value.id);
    if (juvenileParentInfoId) {
      await createJuvenileParents({parents}, juvenileParentInfoId);
      const resp = await fetchJuvenileInfoDetailById(selectedJuvenile.value.id);
      emit('select-juvenile', resp);
      alertIt('Muvofaqiyatli qo`shildi !', 'success');
      emit('save-juvenile-handler');
      setClearForm(true);
    }
  } catch (e) {
    console.log(e);
  }
};

const changeMode = ref(false);

const handleChangeMode = (val) => {
  changeMode.value = val;
};
</script>

<style scoped></style>
