<template>
  <form class="h-full" @submit="onSubmit">
    <section class="section_wrapper">
      <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label class="input_label" for="address_region"> Viloyat / Shahar </label>
          <SelectBox id="address_region" v-model="address_region" :options="regionsList" show-by="name" track-by="id" placeholder="Viloyatni tanlang" @input="addressFetchDistrictsByRegionId" />
          <span class="error_message">{{ address_regionError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="address_district"> Tuman </label>
          <SelectBox id="address_district" v-model="address_district" :options="addressDistrictList" show-by="name" track-by="id" placeholder="Tumanni tanlang" @input="fetchMahallaByDistrictId" />
          <span class="error_message">{{ address_districtError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="address_mahalla"> Mahalla </label>
          <SelectBox id="address_mahalla" v-model="address_mahalla" :options="mahallaList" show-by="name" track-by="id" placeholder="Mahallani tanlang" />
          <span class="error_message">{{ address_mahallaError }}</span>
        </div>
        <div class="form_wrapper my-3 col-span-2 xl:col-span-3">
          <label class="input_label" for="address"> Ko'cha,uy </label>
          <input v-validate-cyrillic id="address" v-model="address" placeholder="Ko'cha , uyni kiriting" class="base_input" name="address" type="text" />
          <span class="error_message">{{ addressError }}</span>
        </div>
      </div>
    </section>
    <button class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Saqlash</button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {GCP} from '../../../../services/gcp.service';
import {requiredValidatorMessage} from '../../../../utils/validatorMessages';
import useRegion from '../../../../composables/selectbox-list/useRegion';
import useDistrict from '../../../../composables/selectbox-list/useDistrict';
import SelectBox from '../../../Common/SelectBox';
import useMahallas from '../../../../composables/selectbox-list/useMahallas';
import useScrollToError from '../../../../composables/useScrollToError';
import {defineEmits, defineProps, onMounted, toRefs, watch} from 'vue';
import useAlert from '../../../../composables/useAlert';
import {useCyrillicToLatin} from '../../../../composables/useCyrillicToLatin';

const {alertIt} = useAlert();

const emit = defineEmits(['submit']);
const {handleSubmit, isSubmitting, resetForm} = useForm();
const {regionsList} = useRegion();
const {mahallaList, fetchMahallaByDistrictId} = useMahallas();
const {districtsList: addressDistrictList, fetchDistrictsByRegionId: addressFetchDistrictsByRegionId} = useDistrict();
const {scrollToError} = useScrollToError();

const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
  selectedJuvenile: {
    type: Object,
  },
});
const {initialFields, clearFormMode, selectedJuvenile} = toRefs(props);
onMounted(async () => {
  setTimeout(async () => {
    const {data, status} = await GCP.getAddressInfo({pinfl: selectedJuvenile.value?.personal_info?.pinfl});
    if (status === 200) {
      if (data?.AnswereId === 1) {
        if (regionsList.value.length) {
          address_region.value = regionsList.value.find((n) => n.id === data.Data.PermanentRegistration.Region.our_region_id).id;
          address.value = await useCyrillicToLatin(data.Data.PermanentRegistration.Address);
          await addressFetchDistrictsByRegionId({target: address_region});
        }
        // await addressFetchDistrictsByRegionId({target: {value: address_region.value.id}});
        // address_region.value = addressDistrictList.value.find((n) => n.id === data.Data.PermanentRegistration.District.our_region_id);
      } else {
        alertIt(data?.AnswereMessage, 'error');
      }
    }
  }, 500);
});

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

function initializeFields(fieldsFromServer) {
  if (fieldsFromServer.address_region) {
    // address_region.value = fieldsFromServer.address_region
    // address_district.value = fieldsFromServer.address_district
    // address_mahalla.value = fieldsFromServer.address_mahalla
    // addressFetchDistrictsByRegionId({target:{value:fieldsFromServer.address_region}})
  }
}

onMounted(() => {
  if (initialFields.value) {
    initializeFields(initialFields.value);
  }
});

function onInvalidSubmit({errors}) {
  console.log(errors);
  scrollToError(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async ({address_region, address_district, address_mahalla, address}) => {
  try {
    const payloadObj = {
      address_mahalla,
      address,
    };
    const formData = new FormData();

    Object.keys(payloadObj).forEach((k) => {
      formData.append(k, payloadObj[k]);
    });
    emit('submit', formData);
    // resetForm()
  } catch (e) {
    console.log(e);
  }
}, onInvalidSubmit);

const {value: address_region, errorMessage: address_regionError} = useField('address_region', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: address_district, errorMessage: address_districtError} = useField('address_district', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: address_mahalla, errorMessage: address_mahallaError} = useField('address_mahalla', yup.string().required(requiredValidatorMessage), {initialValue: ''});

const {value: address, errorMessage: addressError} = useField('address', yup.string().required(requiredValidatorMessage), {initialValue: ''});

watch(
  () => address_region.value,
  (value) => {
    console.log(value);
  }
);
</script>

<style scoped></style>
