import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function usePassportTypes() {
  const passportTypes = ref([]);
  const fetchPassportTypes = async () => {
    const { data } = await InfoService.fetchPassportTypes();
    passportTypes.value = data;
  };
  onMounted(async () => {
    await fetchPassportTypes();
  });

  return {
    passportTypes,
  };
}
