import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useMaritalStatuses() {
  const maritalStatuses = ref([]);
  const fetchMaritalStatuses = async () => {
    const { data } = await InfoService.fetchMaritalStatuses();
    maritalStatuses.value = data;
  };
  onMounted(async () => {
    await fetchMaritalStatuses();
  });

  return {
    maritalStatuses,
  };
}
