import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useCollegesList() {
  const collegesList = ref([]);
  const fetchCollegesListByRegionId = async (val) => {
    const {data} = await InfoService.fetchCollegesListByRegionId(val);
    collegesList.value = data;
  };

  return {
    collegesList,
    fetchCollegesListByRegionId,
  };
}
