<template>
  <h3 class="my-4 text-2xl">Shaxsiy ma'lumotlar</h3>
  <JuvenilePersonalInfoCard v-if="selectedJuvenile && selectedJuvenile.personal_info?.passport_type?.id" :personal_info="selectedJuvenile.personal_info" />
  <div v-if="selectedJuvenile.parent_info && selectedJuvenile?.is_been_in_center" class="flex justify-end my-[24px]">
    <button class="px-[12px] py-[6px] bg-primary text-white rounded" @click="createAvailableJuvenileHandler">Markazga qayta qabul qilish</button>
  </div>
  <PersonalInformationForm v-if="!selectedJuvenile || !selectedJuvenile.personal_info?.passport_type?.id" :clear-form-mode="clearForm" @submit="onSubmit" @juvenile-is-found="setFoundJuvenile" />
</template>

<script setup>
import PersonalInformationForm from './JuvenilePersonalInformationForm';
import JuvenilesService from '../../../../services/juveniles.service';
import useClearForm from '../../../../composables/useClearForm';
import useAlert from '../../../../composables/useAlert';
import {defineEmits, defineProps, toRefs, watch} from 'vue';
import JuvenilePersonalInfoCard from './JuvenilePersonalInfoCard';
import useErrorAlert from '../../../../composables/useErrorAlert';
import {useRouter} from 'vue-router';

const router = useRouter();

const props = defineProps({
  selectedJuvenile: {
    type: Object,
    required: true,
  },
});

const {selectedJuvenile} = toRefs(props);

const emit = defineEmits(['select-juvenile']);
const {clearForm, setClearForm} = useClearForm();
const {createJuvenilePersonalInfo, fetchJuvenileInfoDetailById, createAvailableJuvenile} = JuvenilesService;
const {alertIt} = useAlert();
const {alertThisError} = useErrorAlert();
const onSubmit = async (formData) => {
  if (selectedJuvenile?.value && selectedJuvenile.value?.personal_info?.passport_type?.id) {
    formData.append('is_in_identified_list', false);
  } else if (selectedJuvenile?.value && selectedJuvenile.value?.personal_info && !selectedJuvenile.value?.personal_info?.passport_type?.id) {
    formData.append('is_in_identified_list', true);
    formData.append('juvenile_id', selectedJuvenile.value.id);
  }
  try {
    const {
      data: {message, juvenile_id},
    } = await createJuvenilePersonalInfo(formData);
    if (juvenile_id) {
      alertIt(message, 'success');
      setClearForm(true);
      const resp = await fetchJuvenileInfoDetailById(juvenile_id);
      if (resp?.personal_info?.passport_type?.id === 5) {
        emit('select-juvenile', resp, 'first');
      } else {
        emit('select-juvenile', resp);
      }
    }
  } catch (e) {
    alertThisError(e);
  }
};

const createAvailableJuvenileHandler = async () => {
  try {
    await createAvailableJuvenile(selectedJuvenile.value?.id);
    alertIt('Bola markazga muvaffaqqiyatli qayta qabul qilindi!');
    router.push('/');
  } catch (e) {
    alertThisError(e);
  }
};

const setFoundJuvenile = (juvenile) => {
  emit('select-juvenile', juvenile, 'first');
};
</script>

