import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useKindergartens() {
  const kindergartensList = ref([]);
  const fetchKindergartensByDistrictId = async (val) => {
    console.log(val);
    const {data} = await InfoService.fetchKindergartens(val);
    kindergartensList.value = data;
  };

  return {
    kindergartensList,
    fetchKindergartensByDistrictId,
  };
}
