import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useLitseysList() {
  const litseysList = ref([]);
  const fetchLitseysListByRegionId = async (val) => {
    const {data} = await InfoService.fetchLitseysListByRegionId(val);
    litseysList.value = data;
  };

  return {
    litseysList,
    fetchLitseysListByRegionId,
  };
}
