import axios from './api/axios';

const ParentsService = {
  async fetchJuvenileParents(id) {
    const {data} = await axios.get(`/api/juvenile/juveniles/${id}`);
    return data?.parent;
  },
  async createJuvenileParents(formData, id) {
    return await axios.post('/api/juvenile/juvenile_parents/', formData, {
      params: {
        parent_info_juvenile_id: id,
      },
    });
  },
  async createJuvenileGuardianInfo(formData, id) {
    return await axios.post(`/api/juvenile/juveniles/${id}/juvenile_parentinfo_create_or_update/`, formData);
  },
  async deleteJuvenileParent(juvenileId, parentId) {
    return await axios.delete(`/api/juvenile/juvenile_parents/${parentId}/`, {
      params: {
        juvenile_id: juvenileId,
      },
    });
  },
};
export default ParentsService;
