<template>
  <h3 class="my-4 text-2xl">
    Yashash manzil
  </h3>
  <AddressInfoCard v-if="selectedJuvenile.address_info" :address_info="selectedJuvenile.address_info" />
  <div v-if="selectedJuvenile.address_info && selectedJuvenile?.is_been_in_center" class="flex justify-end my-[24px]">
    <button v-if="!changeMode" class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(true)">
      O'zgartirish
    </button>
    <button v-else class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(false)">
      Bekor qilish
    </button>
  </div>
  <div v-if="changeMode || !selectedJuvenile.address_info" class="p-[12px] shadow">
    <ResidentialAddressForm :clear-form-mode="clearForm" @submit="onSubmit" :selectedJuvenile="selectedJuvenile" />
  </div>
</template>

<script setup>
import ResidentialAddressForm from "./ResidentialAddressForm";
import AddressInfoCard from "./AddressInfoCard";
import useClearForm from "../../../../composables/useClearForm";
import useAlert from "../../../../composables/useAlert";
import JuvenilesService from "../../../../services/juveniles.service";
import {defineEmits, defineProps, ref, toRefs} from 'vue'

const props = defineProps({
  selectedJuvenile:{
    type:Object,
    required:true
  }
})
const {selectedJuvenile}  = toRefs(props)

const {clearForm,setClearForm} = useClearForm()
const {alertIt} = useAlert()
const {createJuvenileAddressInfo,fetchJuvenileInfoDetailById} = JuvenilesService
const emit = defineEmits(['select-juvenile'])

const changeMode = ref(false)

const handleChangeMode = (val)=>{
  changeMode.value = val
}

const onSubmit = async (formData)=>{
   try {
     await createJuvenileAddressInfo(selectedJuvenile.value.id,formData)
     const resp = await fetchJuvenileInfoDetailById(selectedJuvenile.value.id)
     emit('select-juvenile',resp)
     alertIt('Mahalla qoshildi','success')
     setClearForm(true)
   }catch (e) {
     console.log(e)
   }

}
</script>

