<template>
  <form @submit="onSubmit">
    <section class="section_wrapper">
      <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label class="input_label" for="maritalStatus"> Bolaning holati </label>
          <SelectBox id="marital_status" v-model="marital_status" :options="maritalStatuses" show-by="text" track-by="id" placeholder="Holatini tanlang" />
          <span class="error_message">{{ marital_statusError }}</span>
        </div>
      </div>
    </section>
    <section class="section_wrapper">
      <AddParentsForm v-if="allowToShowParents" v-model:parents-list="parents" :marital-status="marital_status" @delete-parent="deleteParent" />
      <span class="error_message">{{ parentsError }}</span>
    </section>
    <button class="px-3 m-2 py-2 border bg-primary text-white rounded-md uppercase" type="submit" :disabled="isSubmitting">bolani saqlash</button>
  </form>
</template>

<script setup>
import AddParentsForm from '../../../AddParentsForm';
import {computed, defineEmits} from 'vue';
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import useMaritalStatuses from '../../../../composables/selectbox-list/useMaritalStatuses';
import {requiredValidatorMessage} from '../../../../utils/validatorMessages';
import SelectBox from '../../../Common/SelectBox';

const HAS_NOT_PARENTS = 4;
const {maritalStatuses} = useMaritalStatuses();

function onInvalidSubmit({errors}) {
  console.log(errors);
}

const emit = defineEmits(['submit', 'marital-status-change']);
// const props = defineProps({
//   initialFields:{
//     type:Object,
//     default:function (){
//       return null
//     }
//   },
//   clearFormMode:{
//     type:Boolean,
//     default:false
//   },
//   isEditMode:{
//     type:Boolean,
//     default:false
//   }
// })
// const {initialFields,isEditMode,clearFormMode} = toRefs(props)
const {handleSubmit, isSubmitting, resetForm} = useForm();

const onSubmit = handleSubmit(async ({marital_status, parents}) => {
  try {
    const payloadObj = {
      marital_status,
      parents,
    };
    // const formData = new FormData();
    // Object.keys(payloadObj).forEach(k => {
    //   // formData.append(k, payloadObj[k])
    //   if ( k === 'parents') {
    //     formData.append(k, JSON.stringify(payloadObj[k], null, 1))
    //   } else {
    //    formData.append(k, payloadObj[k])
    //   }
    // })
    emit('submit', payloadObj);
    resetForm();
  } catch (e) {
    console.log(e);
  }
}, onInvalidSubmit);

const {value: marital_status, errorMessage: marital_statusError} = useField('marital_status', yup.number().required(requiredValidatorMessage));

const allowToShowParents = computed(() => {
  return !(marital_status.value === HAS_NOT_PARENTS);
});
const parentIsRequired = computed(() => {
  return marital_status.value !== HAS_NOT_PARENTS;
});

const dynamicValidatorForParent = computed(() => {
  return !parentIsRequired.value ? yup.array().notRequired() : yup.array().min(1, 'Bolaning boquvchisini kiritishingiz shart');
});

function deleteParent(deletedParent) {
  parents.value = parents.value.filter((p) => p.pinfl !== deletedParent.pinfl);
}

const {value: parents, errorMessage: parentsError} = useField('parents', dynamicValidatorForParent, {
  initialValue: [],
});
</script>

<style scoped></style>
