<template>
  <form @submit="onSubmit">
    <section class="section_wrapper">
      <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
        <div class="form_wrapper">
          <label class="input_label" for="school_type"> Muassasa turi </label>
          <SelectBox id="school_type" v-model="school_type" :options="schoolTypes" show-by="text" track-by="id" placeholder="Turini tanlang" />
          <span class="error_message">{{ school_typeError }}</span>
        </div>
        <div v-if="!schoolTypeIsNotStudyingOrNotWorking" class="form_wrapper">
          <label class="input_label" for="school_region"> Muassasa joylashgan viloyat </label>
          <SelectBox id="school_region" v-model="school_region" :options="regionsList" show-by="name" track-by="id" placeholder="Viloyatni tanlang" @input="fetchDataByRegionId" />
          <span class="error_message">{{ school_regionError }}</span>
        </div>
        <div v-if="!schoolTypeIsNotStudyingOrNotWorking" class="form_wrapper">
          <label class="input_label" for="school_district"> Muassasa joylashgan tuman </label>
          <SelectBox id="school_district" v-model="school_district" :options="educationDistrictList" show-by="name" track-by="id" placeholder="Tumanni tanlang" @input="districtSelectHandler" />
          <span class="error_message">{{ school_districtError }}</span>
        </div>
        <div v-if="schoolTypeIsKindergarten" class="form_wrapper">
          <label class="input_label" for="kindergarten"> Maktabgacha ta'lim muassasasi </label>
          <SelectBox id="kindergarten" v-model="kindergarten" :options="kindergartensList" show-by="name" track-by="id" placeholder="Maktabgacha ta'lim muassasasini tanlang" />
          <span class="error_message">{{ kindergartenError }}</span>
        </div>
        <div v-if="schoolTypeIsSchool" class="form_wrapper">
          <label class="input_label" for="school"> Maktab </label>
          <SelectBox id="school" v-model="school" :options="schoolList" show-by="name" track-by="id" placeholder="Maktabni tanlang" />
          <span class="error_message">{{ schoolError }}</span>
        </div>
        <div v-if="schoolTypeIsUniversity" class="form_wrapper">
          <label class="input_label" for="school"> Oliy ta'lim muassasasi </label>
          <SelectBox id="university" v-model="university" :options="universitiesList" show-by="name" track-by="id" placeholder="Oliy ta'lim muassasasini tanlang" />
          <span class="error_message">{{ universityError }}</span>
        </div>
        <div v-if="schoolTypeIsLitsey" class="form_wrapper">
          <label class="input_label" for="school"> Litsey </label>
          <SelectBox id="litsey" v-model="litsey" :options="litseysList" show-by="name" track-by="id" placeholder="Litseyni tanlang" />
          <span class="error_message">{{ litseyError }}</span>
        </div>
        <div v-if="schoolTypeIsVocationalSchool" class="form_wrapper">
          <label class="input_label" for="vocational_school"> Kasb-hunar maktabi </label>
          <SelectBox id="vocational_school" v-model="vocational_school" :options="vocationalSchoolsList" show-by="name" track-by="id" placeholder="Kasb-hunar maktabini tanlang" />
          <span class="error_message">{{ vocational_schoolError }}</span>
        </div>
        <div v-if="schoolTypeIsSpecialEducation" class="form_wrapper">
          <label class="input_label" for="vocational_school"> Maxsus ta'lim </label>
          <SelectBox id="special_education" v-model="special_education" :options="specialEducationList" show-by="name" track-by="id" placeholder="Maxsus ta'limni tanlang" />
          <span class="error_message">{{ special_educationError }}</span>
        </div>
        <div v-if="schoolTypeIsCollege" class="form_wrapper">
          <label class="input_label" for="college"> Kasb hunar kollej </label>
          <SelectBox id="college" v-model="college" :options="collegesList" show-by="name" track-by="id" placeholder="Kasb hunar kollejni tanlang" />
          <span class="error_message">{{ collegeError }}</span>
        </div>
        <div v-if="schoolTypeIsTexnikum" class="form_wrapper">
          <label class="input_label" for="texnikum"> Texnikum </label>
          <SelectBox id="texnikum" v-model="texnikum" :options="texnikumsList" show-by="name" track-by="id" placeholder="Texnikumni tanlang" />
          <span class="error_message">{{ texnikumError }}</span>
        </div>
      </div>
    </section>
    <button class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Saqlash</button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import SelectBox from '../../../Common/SelectBox';
import * as yup from 'yup';
import {requiredValidatorMessage} from '../../../../utils/validatorMessages';
import {computed, defineEmits, defineProps, onMounted, toRefs, watch} from 'vue';
import useSchoolTypes from '../../../../composables/selectbox-list/useSchoolTypes';
import useDistrict from '../../../../composables/selectbox-list/useDistrict';
import useRegion from '../../../../composables/selectbox-list/useRegion';
import useKindergartens from '../../../../composables/selectbox-list/useKindergartens';
import useSchoolList from '../../../../composables/selectbox-list/useSchoolList';
import useUniversitiesList from '../../../../composables/selectbox-list/useUniversitiesList';
import useLitseysList from '../../../../composables/selectbox-list/useLitseysList';
import useVocationalSchoolsList from '../../../../composables/selectbox-list/useVocationalSchoolsList';
import useSpecialEducationList from '../../../../composables/selectbox-list/useSpecialEducationList';
import useCollegesList from '../../../../composables/selectbox-list/useCollegesList';
import useTexnikumsList from '../../../../composables/selectbox-list/useTexnikumsList';

const {handleSubmit, isSubmitting, resetForm} = useForm();
const emit = defineEmits(['submit']);

const {schoolTypes} = useSchoolTypes();
const {fetchKindergartensByDistrictId, kindergartensList} = useKindergartens();
const {fetchSchoolsByDistrictId, schoolList} = useSchoolList();
const {fetchUniversitiesListByRegionId, universitiesList} = useUniversitiesList();
const {fetchLitseysListByRegionId, litseysList} = useLitseysList();
const {fetchCollegesListByRegionId, collegesList} = useCollegesList();
const {fetchTexnikumsListByRegionId, texnikumsList} = useTexnikumsList();
const {fetchVocationalSchoolsListByRegionId, vocationalSchoolsList} = useVocationalSchoolsList();
const {fetchSpecialEducationListByRegionId, specialEducationList} = useSpecialEducationList();
const {regionsList} = useRegion();
const {districtsList: educationDistrictList, fetchDistrictsByRegionId: fetchDistrictsForEducationByRegionId} = useDistrict();

const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
});
const {initialFields, clearFormMode} = toRefs(props);

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

function initializeFields(fieldsFromServer) {
  // if(fieldsFromServer.school_region){
  //   school_region.value = fieldsFromServer.school_region
  //   school_district.value = fieldsFromServer.school_district
  //   fetchDistrictsForEducationByRegionId({target:{value:fieldsFromServer.school_region}})
  // }
}

onMounted(() => {
  if (initialFields.value) {
    initializeFields(initialFields.value);
  }
});

const NOT_WORKING_NOT_STUDYING = 9;
const IS_WORKING = 10;
const KINDERGARTEN = 1;
const SCHOOL = 2;
const UNIVERSITY = 8;
const LITSEY = 5;
const VOCATIONAL_SCHOOL = 3;
const SPECIAL_EDUCATION = 7;
const COLLEGE = 4;
const TEXNIKUM = 6;

function onInvalidSubmit({errors}) {
  console.log(errors);
}

const onSubmit = handleSubmit(async ({school_region, texnikum, school_district, school_type, kindergarten, school, university, litsey, vocational_school, college, special_education}) => {
  try {
    const payloadObj = {
      school_type,
    };
    school_region ? (payloadObj.school_region = school_region) : null;
    litsey ? (payloadObj.litsey = litsey) : null;
    school_district ? (payloadObj.school_district = school_district) : null;
    kindergarten ? (payloadObj.kindergarten = kindergarten) : null;
    school ? (payloadObj.school = school) : null;
    university ? (payloadObj.university = university) : null;
    vocational_school ? (payloadObj.vocational_school = vocational_school) : null;
    college ? (payloadObj.college = college) : null;
    texnikum ? (payloadObj.texnikum = texnikum) : null;
    special_education ? (payloadObj.special_education = special_education) : null;

    const formData = new FormData();

    Object.keys(payloadObj).forEach((k) => {
      formData.append(k, payloadObj[k]);
    });
    emit('submit', formData);
    // resetForm()
  } catch (e) {
    console.log(e);
  }
}, onInvalidSubmit);

const {value: school_type, errorMessage: school_typeError} = useField('school_type', yup.number().required(requiredValidatorMessage));

const dynamicValidatorForSchoolName = computed(() => {
  if (school_type.value === NOT_WORKING_NOT_STUDYING || school_type.value === IS_WORKING || school_type.value === KINDERGARTEN || school_type.value === SCHOOL) {
    return yup.string().notRequired();
  } else return yup.string().required(requiredValidatorMessage);
});

const {value: school_district, errorMessage: school_districtError} = useField('school_district', dynamicValidatorForSchoolName, {initialValue: ''});

const {value: school_region, errorMessage: school_regionError} = useField('school_region', dynamicValidatorForSchoolName, {
  initialValue: '',
});

const schoolTypeIsNotStudyingOrNotWorking = computed(() => school_type.value === NOT_WORKING_NOT_STUDYING || school_type.value === IS_WORKING);
const schoolTypeIsKindergarten = computed(() => school_type.value === KINDERGARTEN);
const schoolTypeIsSchool = computed(() => school_type.value === SCHOOL);
const schoolTypeIsUniversity = computed(() => school_type.value === UNIVERSITY);
const schoolTypeIsLitsey = computed(() => school_type.value === LITSEY);
const schoolTypeIsVocationalSchool = computed(() => school_type.value === VOCATIONAL_SCHOOL);
const schoolTypeIsSpecialEducation = computed(() => school_type.value === SPECIAL_EDUCATION);
const schoolTypeIsCollege = computed(() => school_type.value === COLLEGE);
const schoolTypeIsTexnikum = computed(() => school_type.value === TEXNIKUM);

const districtSelectHandler = (val) => {
  if (schoolTypeIsKindergarten.value) {
    fetchKindergartensByDistrictId(val);
  }
  if (schoolTypeIsSchool.value) {
    fetchSchoolsByDistrictId(val);
  }
};

const fetchDataByRegionId = async (val) => {
  await fetchDistrictsForEducationByRegionId(val);
  if (schoolTypeIsUniversity.value) {
    await fetchUniversitiesListByRegionId(val);
  }
  if (schoolTypeIsLitsey.value) {
    await fetchLitseysListByRegionId(val);
  }
  if (schoolTypeIsVocationalSchool.value) {
    await fetchVocationalSchoolsListByRegionId(val);
  }
  if (schoolTypeIsSpecialEducation.value) {
    await fetchSpecialEducationListByRegionId(val);
  }
  if (schoolTypeIsCollege.value) {
    await fetchCollegesListByRegionId(val);
  }
  if (schoolTypeIsTexnikum.value) {
    await fetchTexnikumsListByRegionId(val);
  }
};

const dynamicValidatorForKindergarten = computed(() => {
  return schoolTypeIsKindergarten.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});

const dynamicValidatorForSchool = computed(() => {
  return schoolTypeIsSchool.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForUniversity = computed(() => {
  return schoolTypeIsUniversity.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForLitsey = computed(() => {
  return schoolTypeIsLitsey.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForVocationalSchool = computed(() => {
  return schoolTypeIsVocationalSchool.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForSpecialEducation = computed(() => {
  return schoolTypeIsSpecialEducation.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForCollege = computed(() => {
  return schoolTypeIsCollege.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const dynamicValidatorForTexnikum = computed(() => {
  return schoolTypeIsTexnikum.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});
const {value: kindergarten, errorMessage: kindergartenError} = useField('kindergarten', dynamicValidatorForKindergarten, {initialValue: ''});

const {value: school, errorMessage: schoolError} = useField('school', dynamicValidatorForSchool, {initialValue: ''});

const {value: university, errorMessage: universityError} = useField('university', dynamicValidatorForUniversity, {initialValue: ''});

const {value: litsey, errorMessage: litseyError} = useField('litsey', dynamicValidatorForLitsey, {initialValue: ''});

const {value: vocational_school, errorMessage: vocational_schoolError} = useField('vocational_school', dynamicValidatorForVocationalSchool, {initialValue: ''});

const {value: special_education, errorMessage: special_educationError} = useField('special_education', dynamicValidatorForSpecialEducation, {initialValue: ''});

const {value: college, errorMessage: collegeError} = useField('college', dynamicValidatorForCollege, {initialValue: ''});

const {value: texnikum, errorMessage: texnikumError} = useField('texnikum', dynamicValidatorForTexnikum, {initialValue: ''});

watch(
  () => school_type.value,
  (newType, prevType) => {
    if (newType === NOT_WORKING_NOT_STUDYING || newType === IS_WORKING) {
      school_district.value = '';
    }
    if (newType !== KINDERGARTEN && kindergarten.value) {
      kindergarten.value = '';
    }
    if (newType !== SCHOOL && school.value) {
      school.value = '';
    }
    if (newType !== UNIVERSITY && university.value) {
      university.value = '';
    }
    if (newType !== LITSEY && litsey.value) {
      litsey.value = '';
    }
    if (newType !== COLLEGE && college.value) {
      college.value = '';
    }
    if (newType !== TEXNIKUM && texnikum.value) {
      texnikum.value = '';
    }
    if (newType !== VOCATIONAL_SCHOOL && vocational_school.value) {
      vocational_school.value = '';
    }
    if (newType !== SPECIAL_EDUCATION && special_education.value) {
      special_education.value = '';
    }
    if (!kindergartensList.value.length && schoolTypeIsKindergarten.value && school_district.value) {
      fetchKindergartensByDistrictId({target: {value: school_district.value}});
    }
    if (!schoolList.value.length && schoolTypeIsSchool.value && school_district.value) {
      fetchSchoolsByDistrictId({target: {value: school_district.value}});
    }

    if (newType && school_region.value && newType !== prevType) {
      fetchDataByRegionId({target: {value: school_region.value}});
    }
  }
);
</script>

<style scoped></style>
