import { ref } from "vue";
import RegionsService from "../../services/regions-districts.service";

export default function useMahallas() {
  const mahallaList = ref([]);
  const fetchMahallaByDistrictId = async (val) => {
    const { data } = await RegionsService.fetchMahallaByDistrictId(val);
    mahallaList.value = data;
  };

  return {
    mahallaList,
    fetchMahallaByDistrictId,
  };
}
