import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useUniversitiesList() {
  const universitiesList = ref([]);
  const fetchUniversitiesListByRegionId = async (val) => {
    const {data} = await InfoService.fetchUniversitiesListByRegionId(val);
    universitiesList.value = data;
  };

  return {
    universitiesList,
    fetchUniversitiesListByRegionId,
  };
}
