<template>
  <main class="w-full">
    <section class="flex justify-between mb-[12px] bg-white rounded-md p-[4px]">
      <button class="px-[18px] group rounded-md py-[6px] bg-white text-[#4785FE] hover:text-white hover:bg-[#4785FE] flex items-center" @click="selectJuvenileModalRef.openModal()">
        <Icon class="w-[25px] h-[25px] text-[#4785FE] group-hover:text-[#fff]" icon="mdi:people-add" />
        Yakunlanmagan bolalar
      </button>
    </section>
    <section v-if="selectedJuvenile.id" class="flex bg-white mb-[12px] p-[12px] items-end justify-between rounded-md">
      <PassportCard class="max-w-[700px]" :passport-info="selectedJuvenile.personal_info" @canceled="deleteSelectedJuvenile" />
    </section>
    <section class="rounded-md">
      <div class="flex">
        <button
          v-for="tab in tabs"
          :key="tab.id"
          class="px-[15px] py-[10px]"
          :class="tab.id === activeTab?.id ? 'text-[#4785FE] bg-white' : 'text-[#0B0B0B]'"
          :disabled="tab.disabled"
          @click="tabClickHandler(tab)"
        >
          <span class="flex items-center gap-1">
            {{ tab.title }}
            <Icon v-if="tab.filled" class="text-lg text-[#4785FE]" icon="mdi:check" />
          </span>
        </button>
      </div>
      <div class="bg-white p-[15px]">
        <component :is="renderTabComponent" :selected-juvenile="selectedJuvenile" @select-juvenile="selectJuvenileHandler" @save-juvenile-handler="saveJuvenileHandler" />
      </div>
    </section>
  </main>
  <ModalComponent ref="selectJuvenileModalRef" modal-position="center">
    <template #slotBody>
      <TableJuvenilesForDetermine class="min-h-[400px]" @select-juvenile="selectJuvenileHandler($event, 'first')" />
    </template>
  </ModalComponent>
  <ModalComponent ref="modalComponentRef" modalClass="!w-auto" modal-position="center" :close-on-click-away="false" :hide-close-button="true">
    <template #slotBody>
      <div class="p-10 text-3xl flex items-center justify-center">
        <h1>Bola ma'lumotlari muvaffaqiyatli saqlandi</h1>
        <Icon icon="mdi:check-circle" class="mx-2 text-primary" width="34" height="34" />
      </div>
      <div class="flex justify-center">
        <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="modalComponentRef.closeModal">Davom etish</button>
        <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="$router.push('/')">Bosh sahifa</button>
      </div>
    </template>
  </ModalComponent>
</template>

<script setup>
import {computed, markRaw, onMounted, onUnmounted, ref, watch} from 'vue';
import JuvenilePersonalInformation from '../components/CreateJuvenile/Determined/JuvenilePersonalInformation/JuvenilePersonalInformation';
import ResidentialAddress from '../components/CreateJuvenile/Determined/ResidentialAddress/ResidentialAddress';
import JuvenileEducation from '../components/CreateJuvenile/Determined/JuvenileEducation/JuvenileEducation';
import ChildsGuardian from '../components/CreateJuvenile/Determined/ChildsGuardian/ChildsGuardian';
import PassportCard from '../components/PassportCard';
import {Icon} from '@iconify/vue';
import ModalComponent from '../components/Common/Modal/ModalComponent';
import TableJuvenilesForDetermine from '../components/Tables/TableJuvenilesForDetermine';
import useAlert from '../composables/useAlert';

const modalComponentRef = ref();

const selectedJuvenile = ref({
  id: '',
  personal_info: null,
  address_info: null,
  parent_info: null,
  education_info: null,
});

const addressInfoIsDisabled = computed(() => {
  return !selectedJuvenile?.value?.personal_info?.passport_type?.id;
});

const educationInfoIsDisabled = computed(() => {
  return !(selectedJuvenile?.value?.personal_info?.passport_type?.id && selectedJuvenile?.value?.address_info?.id);
});

const parentInfoIsDisabled = computed(() => {
  return !(selectedJuvenile?.value?.personal_info && selectedJuvenile?.value?.address_info && selectedJuvenile?.value?.education_info);
});

const tabs = computed(() => {
  if (selectedJuvenile?.value?.personal_info?.passport_type?.id === 5) {
    return [
      {
        title: "Shaxsiy ma'lumotlar",
        id: 'personal_info',
        component: markRaw(JuvenilePersonalInformation),
        disabled: false,
      },
    ];
  } else
    return [
      {
        title: "Shaxsiy ma'lumotlar",
        id: 'personal_info',
        component: markRaw(JuvenilePersonalInformation),
        disabled: false,
        filled: false,
      },
      {
        title: 'Yashash manzili',
        id: 'address_info',
        component: markRaw(ResidentialAddress),
        disabled: addressInfoIsDisabled.value,
        filled: false,
      },
      {
        title: 'Ta’lim',
        id: 'education_info',
        component: markRaw(JuvenileEducation),
        disabled: educationInfoIsDisabled.value,
        filled: false,
      },
      {
        title: "Bolaning boquvchisi haqida ma'lumotlar",
        id: 'parent_info',
        component: markRaw(ChildsGuardian),
        disabled: parentInfoIsDisabled.value,
        filled: false,
      },
    ];
});

onUnmounted(() => {
  deleteSelectedJuvenile();
});

watch(
  () => selectedJuvenile.value,
  () => {
    tabs.value = tabs.value.map((n, i) => {
      if (selectedJuvenile.value[n.id]) {
        n.filled = true;
        return n;
      } else return n;
    });
    console.log(tabs.value);
  }
);

// const {alertIt} = useAlert();

const saveJuvenileHandler = () => {
  modalComponentRef.value.openModal()
  deleteSelectedJuvenile();
};

const activeTab = ref(tabs.value[0]);

const tabViewHandler = (tabPosition) => {
  if (tabPosition === 'first') {
    activeTab.value = tabs.value[0];
  } else if (tabPosition === 'second') {
    activeTab.value = tabs.value[1];
  } else {
    const currentTabIndex = tabs.value.findIndex((tab) => tab.id === activeTab.value.id);
    if (currentTabIndex !== 3) {
      activeTab.value = tabs.value[currentTabIndex + 1];
    } else {
      activeTab.value = tabs.value[0];
    }
  }
};

const tabClickHandler = (tab) => {
  activeTab.value = tab;
};
const renderTabComponent = computed(() => activeTab.value.component);

const deleteSelectedJuvenile = () => {
  selectedJuvenile.value.id = '';
  selectedJuvenile.value.personal_info = null;
  selectedJuvenile.value.education_info = null;
  selectedJuvenile.value.address_info = null;
  selectedJuvenile.value.parent_info = null;
  window.localStorage.removeItem('selected_juvenile');
  tabViewHandler('first');
};

const selectJuvenileModalRef = ref();

onMounted(() => {
  let selectedJuvenileFromLS = JSON.parse(window.localStorage.getItem('selected_juvenile'));
  if (selectedJuvenileFromLS) {
    selectJuvenileHandler(selectedJuvenileFromLS, 'first');
  }
});

const selectJuvenileHandler = (juvenile, tabPosition) => {
  setSelectedJuvenile(juvenile);
  tabViewHandler(tabPosition);
  selectJuvenileModalRef.value.closeModal();
};

const setSelectedJuvenile = (juvenile) => {
  window.localStorage.setItem('selected_juvenile', JSON.stringify(juvenile));
  selectedJuvenile.value = {...juvenile};
};
</script>

<style scoped></style>
