<template>
  <h3 class="my-4 text-2xl">
    Ta`lim
  </h3>
  <EducationInfoCard v-if="selectedJuvenile.education_info" :education_info="selectedJuvenile.education_info" />
  <div v-if="selectedJuvenile.education_info && selectedJuvenile?.is_been_in_center" class="flex justify-end my-[24px]">
    <button v-if="!changeMode" class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(true)">
      O'zgartirish
    </button>
    <button v-else class="px-[12px] py-[6px] bg-primary text-white rounded" @click="handleChangeMode(false)">
      Bekor qilish
    </button>
  </div>
  <div v-if="changeMode || !selectedJuvenile.education_info" class="p-[12px] shadow">
    <JuvenileEducationForm :clear-form-mode="clearForm" @submit="onSubmit" />
  </div>
</template>

<script setup>
import {defineEmits, defineProps, ref, toRefs, watch} from 'vue'
import JuvenileEducationForm from "./JuvenileEducationForm";
import useClearForm from "../../../../composables/useClearForm";
import useAlert from "../../../../composables/useAlert";
import JuvenilesService from "../../../../services/juveniles.service";
import EducationInfoCard from "./EducationInfoCard";
// createJuvenileEducationInfo
const emit = defineEmits(['select-juvenile'])

const props = defineProps({
  selectedJuvenile:{
    type:Object,
    required:true
  },
})

const {selectedJuvenile}  = toRefs(props)

watch(()=>selectedJuvenile.value,(val)=>{
  console.log(val)
},{
  immediate:true
})
const changeMode = ref(false)

const handleChangeMode = (val)=>{
  changeMode.value = val
}

const {clearForm,setClearForm} = useClearForm()
const {alertIt} = useAlert()
const {createJuvenileEducationInfo,fetchJuvenileInfoDetailById} = JuvenilesService

const onSubmit = async (formData)=>{
  try {
    await createJuvenileEducationInfo(selectedJuvenile.value.id,formData)
    const resp = await fetchJuvenileInfoDetailById(selectedJuvenile.value.id)
    emit('select-juvenile',resp)
    alertIt('Muvofaqiyatli qo`shildi !','success')
    setClearForm(true)
  }catch (e) {
    console.log(e)
  }

}
</script>

