import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useVocationalSchoolsList() {
  const vocationalSchoolsList = ref([]);
  const fetchVocationalSchoolsListByRegionId = async (val) => {
    const {data} = await InfoService.fetchVocationalSchoolsListByRegionId(val);
    vocationalSchoolsList.value = data;
  };

  return {
    vocationalSchoolsList,
    fetchVocationalSchoolsListByRegionId,
  };
}
