import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useParentTypes() {
  const parentTypesList = ref([]);
  const fetchParentTypesList = async () => {
    const { data } = await InfoService.fetchParentTypesList();
    parentTypesList.value = data;
  };
  onMounted(async () => {
    await fetchParentTypesList();
  });
  return {
    parentTypesList,
    fetchParentTypesList,
  };
}
